const RobotIcon = props => <Icon component={RobotSvg} {...props} />;
const RobotChangeIcon = props => <Icon component={RobotChangeSvg} {...props} />;

const text = <div>How would you rate your experience?<br/>（你使用 Ant Design 的体验如何？）<br/><br/>
                 <Rate character={<Icon type="heart" />} allowHalf />
            </div>;

export default class Robot extends Component {
    state = {
        mouse: false
    }

    reply = (question) => {
        axios.get(`http://api.tianapi.com/txapi/robot/index?key=a20db40d6c22a56b9e917a1b907d8502&question=${question}`).then(
            response => {
                console.log(response.data.newslist[0].reply);
                return response.data.newslist[0].reply
            },
            error => {
                return error;
            }
        )
    }

    robot = () => {
        alert('hello')
    }

    handleMouse = (flag) => {
		return () => {this.setState({mouse: flag})}
	}

    render() {
        const { mouse } = this.state;
        return (
            <div>
                <Popconfirm
                placement="topRight"
                title={text}
                onConfirm={this.robot}
                okText="Yes"
                cancelText="No"
                >
                    <div className="robot" style={{ backgroundColor: mouse ? '	#E0FFFF' : '' }} onMouseEnter={this.handleMouse(true)} onMouseLeave={this.handleMouse(false)}>
                        {
                            mouse ? <RobotChangeIcon className="robot-icon" /> : <RobotIcon className="robot-icon" />
                        }
                    </div>
                </Popconfirm>
                <div></div>
            </div>
        )
    }
}
